*{
  margin: 0;
  padding: 0;
}
.app-input{
  text-align: center;
  color: blue;
}
h1{
  color: green;
}
.app-input input{
  font-size: 14px;
  border-radius: 5px;
  border: 2px solid grey;
  outline: none;
  padding: 10px 5px;
  width: 200px;
}
.app-input button{
  background-color: rgb(184, 45, 189);
  color: white;
  border: none;
  outline: none;
  margin-top: 10px;
  padding: 10px 30px;
  font-size: 16px;
  cursor: pointer;
}
.navActive{
  color: red;
  font-size: 30px;
}
nav{
  background-color: rgb(128, 177, 37);
  padding: 10px 0;
  display: flex;
  align-items: center;
  margin-top: 5px;
}
nav ul{
  display: flex;
  list-style-type: none;
  align-items: center;
  justify-content: space-between;
  width: 80%;
  margin-left: 10%;
  
  color: white;
  font-weight: bold;
}
nav ul li{
  cursor: pointer;
  font-size: 14px;
  transition: all 0.5s ease-in-out;
}
nav ul li :hover{
  transform: scale(1.5);
}
.app-container{
  background-color: rgb(63, 63, 228);
  color:white;
  padding: 10px;
}